
.saferpay-paymentpage {
  body {
    background-color: transparent;
  }
  div.box.box-content {
    @extend .pt-3;
    @extend .pr-framemd-5;
    @extend .rounded-bottom;
  }
  /* card validation failed Page */
  form.page.page-dcc {
    > header {
      h1 {
        @extend .display-4;
        @extend .pb-2;
      }
      figure img {
        @extend .shadow-lg;
        @extend .m-3;
      }
    }
    main {
      > p {
        @extend .display-21;
      }
      .box.box-dcc {
        .btn.btn-block.btn-toggle {
          @extend .display-21;
        }
        span {
          @extend .display-21;
          @extend .text-dark;
        }
      }
      > .box.box-information {
        @extend .alert;
        @extend .alert-warning;
        @extend .shadow-lg;
        @extend .text-warning;
        @extend .icon-inline;
        @extend .icon-svg;
        @extend .icon-left;
        @extend .icon-envelope;
        @extend .d-flex;
        @extend .mx-3;
        > .icon.icon-information {
          @extend .icon-i-white;
          @extend .bg-warning;
          @extend .rounded-circle;
          @extend .flex-column;
          @extend .flex-shrink-0;
          @extend .mr-2;
          font-size: 0;
        }
        > .text.text-information {
          @extend .flex-column;
          @extend .display-23;
          @extend .text-warning;
        }
      }
    }
    > footer > ul {
      list-style-type: none;
      @extend .p-0;
      @extend .mb-0;
      li.navitem {
        padding-right: $grid-gutter-width / 2;
        @extend .pl-0;
      }
      li.navitem.navitem-back {
        @extend .d-none;
      }
      li.navitem.navitem-next > .btn.btn-next {
        &::before {
          @extend .btn;
          @extend .btn-primary;
          content: 'confirm';
          position: relative;
          display: inline-block;
          height: 100%;
        }
      }
    }
  }
  /* Card Data Form Page */
  form.page.page-card.page-hosted {
    main {
      legend {
        @extend .d-none;
      }
      .form-container {
        @extend .row;
        @extend .no-gutters;
        justify-content: space-between;
        .form-group {
          @extend .col-12;
          @extend .pr-0;
          &:nth-child(1) {
            @extend .col-framemd-7;
          }
          &:nth-child(2) {
            @extend .col-framemd-7;
          }
          &:nth-child(3) {
            @extend .order-last;
          }
          &:nth-child(4) {
            @extend .col-framemd-4;
            @extend .flex-nowrap;
          }
          > div.form-label {
            @extend .row;
            @extend .no-gutters;
            > label {
              @extend .col-12;
              @extend .display-23;
              @extend .mb-0;
              @extend .px-0;
              cursor: pointer;
              > span.icon {
                @extend .d-none;
              }
            }
          }
          > div.form-input {
            @extend .row;
            @extend .no-gutters;
            > input {
              @extend .col-12;
              @extend .form-control;
            }
            > select {
              @extend .col;
              &:nth-child(2) {
                @extend .offset-1;
              }
              @extend .form-control;
              @extend .custom-select;
              @extend .pr-6;
              @extend .bg-white;
            }
            > input.input-verificationcode {
              @extend .col;
              @extend .col-framemd-10;
              height: $input-height;
            }
            > .details-summary-widget {
              @extend .col;
              @extend .offset-1;
              @extend .col-framemd-2;
              @extend .offset-framemd-0;
              @extend .text-framemd-right;
              @extend .p-framemd-0;
              @extend .border-gray-surface;
              line-height: remCalc(40);
              position: relative;
              max-width: 46%;
              @include media-breakpoint-up(framemd) {
                max-width: remCalc(28);
              }
              > span.icon.icon-hint {
                @extend .icon-questionMark-white-20;
                @extend .rounded-circle;
                @extend .bg-dark;
                cursor: pointer;
              }
              > span.text.text-hint {
                @extend .mt-4;
                position: relative;
                max-width: 88vw;
                @include media-breakpoint-up(framemd) {
                  max-width: 75vw;
                }
                line-height: 0;
                color: white;
                float: right;
                overflow: hidden;
                @extend .shadow-lg;
                @extend .bg-white;
                @extend .rounded-bottom;
                @extend .p-3;
                @extend .pt-5;
                @extend .p-framemd-4;
                @extend .border;
                @extend .border-gray;
                @extend .position-relative;
                &::before {
                  content: '';
                  @extend .default-icon-close;
                  display: inline-block;
                  right: remCalc(8);
                  left: auto;
                  position: absolute;
                  top: remCalc(8);
                }
              }
            }
            > input.input-error, input.input-validation-error, select.input-error, select.input-validation-error {
              @extend .border-danger;
              @extend .text-danger;
            }
          }
        }

      }
    }
    footer > ul {
      list-style-type: none;
      @extend .p-0;
      @extend .mb-0;
      li.navitem {
        padding-right: $grid-gutter-width / 2;
        @extend .pl-0;
      }
      .navitem-back {
        @extend .d-none;
      }
      li.navitem.navitem-abort > .btn.btn-abort {
        @extend .d-none;
      }
      li.navitem.navitem-next > .btn.btn-next {
        &::before {
          @extend .btn;
          @extend .btn-primary;
          content: 'confirm';
          position: relative;
          display: inline-block;
          height: 100%;
        }
      }
    }
  }

  .btn-next {
    font-size: 0;
  }
  /* card validation failed Page */
  &[lang='de'] form.page.page-dcc .btn-next::before {
    content: $localization-de-confirm !important;
  }
  &[lang='en-GB'] form.page.page-dcc .btn-next::before {
    content: $localization-en-confirm !important;
  }
  &[lang='nl'] form.page.page-dcc .btn-next::before {
    content: $localization-nl-confirm !important;
  }
  &[lang='fr'] form.page.page-dcc .btn-next::before {
    content: $localization-fr-confirm !important;
  }

  /* Card Data Form Page */
  &[lang='de'] form.page.page-card.page-hosted .btn-next::before {
    content: $localization-de-acceptCreditCard !important;
  }
  &[lang='en-GB'] form.page.page-card.page-hosted .btn-next::before {
    content: $localization-en-acceptCreditCard !important;
  }
  &[lang='nl'] form.page.page-card.page-hosted .btn-next::before {
    content: $localization-nl-acceptCreditCard !important;
  }
  &[lang='fr'] form.page.page-card.page-hosted .btn-next::before {
    content: $localization-fr-acceptCreditCard !important;
  }
}

.dcc-radio.hidden {
  position: static;
  left: auto !important;
}
